body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.skills ul {
  padding: 0;
  list-style-type: none;
}

.projects {
  background-color: rgb(163, 210, 202,0.2);
  margin-bottom: 15px;
  box-shadow: 1px 3px 7px -2px rgba(0,0,0,0.87);
}

.projects:hover {
  background-color: rgb(163, 210, 202,0.8);
}

div {
  font-family: 'Roboto Mono', monospace;
}
